.game_container {
  background-image: conic-gradient(
    #9796f0 45deg,
    #fbc7d4 150deg,
    
    #9796f0 210deg
  );

  min-height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  margin: 0px;
  padding: 0px;
  justify-content: center;
  align-items: center;
}
.emojis_container {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;

  padding: 0px;
}
