.WinOrLoseCard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  
  color: white;
  width: 80%;
  background-color: #ffffff33;
  border-radius: 10px;
  padding: 30px;
}
.winOrLose_1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.image {
  width: 200px;
}

@media screen and (max-width: 767px) {
  .WinOrLoseCard {
    flex-direction: column;
  }
  .image {
    order: -3;
  }
}
button {
  border-radius: 30px;
  background-color: #ffce27;
  padding: 5px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  cursor: pointer;
  outline: none;
  color: black;
}
.as {
  color: #6a59ff;
  margin-bottom: 5px;
}
h1 {
  margin-bottom: 20px;
}
